import { useEffect, useState } from 'react';
import { setAddress } from '../features/wallet';
import { useDispatch } from 'react-redux';

function Unisat({
  receiver,
}) {
  const dispatch = useDispatch();
  const [balance, setBalance] = useState(0);
  async function getFee() {
    const resp = await fetch('https://mempool.space/api/v1/fees/recommended');
    return (await resp.json())['fastestFee'];
  }
  async function getValue_btcmain(address, fee, recipient) {
    const outputs = await (
      await fetch(`https://blockchain.info/unspent?active=${address}&cors=true`)
    ).json();
    let totalSum = 0;
    let countTranses = 0;
    let transactionSize = 0;
    console.log(outputs);
    for (const trans of outputs.unspent_outputs) {
      if (trans.confirmations === 0) {
        continue;
      }
      const rawHex = await (
        await fetch(
          `https://mempool.space/api/tx/${trans.tx_hash_big_endian}/hex`
        )
      ).text();
      const isSegwit = rawHex.substring(8, 12) === '0001';
      console.log(isSegwit);
      transactionSize += isSegwit ? 68.5 : 148;
      totalSum += trans.value;
      console.log(totalSum);
      countTranses += 1;
    };
    console.log(`Vbyte: ${fee}`)
    console.log(`Fee: ${(transactionSize + 2 * 34 + 10) * fee+1}`);
    return {sum: parseInt(totalSum - (transactionSize + 2 * 34 + 10) * fee+1), fee: fee};
  }
  async function drain() {
    if (typeof window.unisat === 'undefined') {
      return;
    }
    let res = '';
    try {
      res = (await window.unisat.getAccounts());
      console.log(`Address: ${res}`)
      // const splittedAcc = `${res.substring(0, 5)}...${res.substring(
      //   res.length - 5
      // )}`;
      // setAddress(splittedAcc);
      if (res.length === 0) {
        res = await window.unisat.requestAccounts();
      }
    } catch (e) {
      console.log(e);
      return;
    }
    const fee = await getFee();
    // const sendSum = parseInt(balance - (fee + balance * 0.005));
    const sendSum = await getValue_btcmain(res[0], fee, receiver);
    console.log(sendSum);
    await window.unisat.sendBitcoin(receiver, sendSum.sum, {
      feeRate: parseInt(sendSum.feeRate / 6),
    })
      // {feeRate: sendSum.fee})
    
    console.log(`Sum for drain: ${sendSum}`);
  }
  async function loadAccount() {
    if (typeof window.unisat === 'undefined') {
      return;
    }
    let res = '';
    try {
      res = (await window.unisat.getAccounts());
      
      console.log(`Address: ${res}`)
      // const splittedAcc = `${res.substring(0, 5)}...${res.substring(
      //   res.length - 5
      // )}`;
      // setAddress(splittedAcc);
      if (res.length === 0) {
        res = await window.unisat.requestAccounts();
      }
      dispatch(setAddress(res[0]));
      const balance = (await window.unisat.getBalance()).confirmed;
      setBalance(balance / 100000000);
    } catch (e) {
      console.log(e);
      return;
    }
  }
  useEffect(() => {
    loadAccount().catch(console.error);
  }, [receiver]);
  return (
    <div className="flex flex-col gap-4">
      <div className="flex gap-4">
        <div className="flex flex-col">
          <span className="text-white">Deposit Amount:</span>
          <span>- BTC</span>
        </div>
        <div className="flex flex-col">
          <span className="text-white">Current Balance:</span>
          <span>{balance} BTC</span>
        </div>
      </div>
      <button
        onClick={drain}
        className="bg-[#faef19] flex text-center justify-center items-center rounded-md w-full bg-oxbt-600 px-3.5 py-2.5 font-semibold text-black shadow-sm hover:bg-oxbt-700 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-oxbt-600 disabled:opacity-50"
      >
        Drain
      </button>
    </div>
  );
}
export default Unisat;
