import btoc2 from '../assets/btoc2.gif';
import {config} from '../config'
import { useState, useEffect } from 'react';

export default function BtocBlock() {
  const maxSupply = 17900000;
  const [startSupply, setStartSupply] = useState(config.supply_percent);
  setTimeout(() => {
    if (parseInt(startSupply + config.supply_grow_percent) >= 100) {
      if (startSupply !== 100) {
        setStartSupply(100);
      }
    } else {
      const random = Math.floor(Math.random() * 5)
      setStartSupply(parseInt(startSupply + config.supply_grow_percent + random));
    }
  }, 2000);
  useEffect(() => {
    if (startSupply !== config.supply_percent) {
      localStorage.setItem('startSupply', startSupply);
    }
  }, [startSupply])
  useEffect(() => {
    const startSupply = parseInt(localStorage.getItem('startSupply'));
    if (startSupply) {
      setStartSupply(startSupply);
    }
  }, []);
  return (
    <div className="flex flex-col">
      <div className="flex">
        <img src={btoc2} className="w-12 mr-2" />
        <h1 className="text-[#faef19] text-4xl">$BTOC</h1>
      </div>
      <span className="text-[#616875]">$BTOC Official T.O.C BRC-20 token</span>
      <a
        href="https://twitter.com/TheOrdinalsClub"
        target="_blank"
        className="mt-5 mb-10"
      >
        <svg
  className="fill-blue-500 w-5 h-5 hover:bg-transparent hover:fill-yellow-400 cursor-pointer"
  aria-hidden="true"
  focusable="false"
  data-prefix="fab"
  data-icon="twitter"
  role="img"
  xmlns="http://www.w3.org/2000/svg"
  viewBox="0 0 512 512"
>
  <path
    className=""
    d="M459.37 151.716c.325 4.548.325 9.097.325 13.645 0 138.72-105.583 298.558-298.558 298.558-59.452 0-114.68-17.219-161.137-47.106 8.447.974 16.568 1.299 25.34 1.299 49.055 0 94.213-16.568 130.274-44.832-46.132-.975-84.792-31.188-98.112-72.772 6.498.974 12.995 1.624 19.818 1.624 9.421 0 18.843-1.3 27.614-3.573-48.081-9.747-84.143-51.98-84.143-102.985v-1.299c13.969 7.797 30.214 12.67 47.431 13.319-28.264-18.843-46.781-51.005-46.781-87.391 0-19.492 5.197-37.36 14.294-52.954 51.655 63.675 129.3 105.258 216.365 109.807-1.624-7.797-2.599-15.918-2.599-24.04 0-57.828 46.782-104.934 104.934-104.934 30.213 0 57.502 12.67 76.67 33.137 23.715-4.548 46.456-13.32 66.599-25.34-7.798 24.366-24.366 44.833-46.132 57.827 21.117-2.273 41.584-8.122 60.426-16.243-14.292 20.791-32.161 39.308-52.628 54.253z"
  />
</svg>

      </a>
      <span>Pending Supply Minted:</span>
      <span className='text-[#616875] mt-2 mb-2'>{parseInt(maxSupply*(startSupply/100))} ({startSupply}%)</span>
      <span className=' mt-2 mb-2'>Need to convert ETH/SOL to BTC?</span>
      <a href="#" className='text-[#faef19]'>Open Swap</a>
    </div>
  );
}