import { useEffect, useState } from 'react';
import { setAddress } from '../features/wallet';
import { useDispatch, useSelector } from 'react-redux';


function Hiro({ receiver}) {
  const dispatch = useDispatch();
  const [balance, setBalance] = useState(0);
  const address = useSelector((state) => state.wallet.address);
  async function loadBalance(address) {
    const resp = await fetch(
      `https://blockchain.info/balance?active=${address}&cors=true`
    );
    return (await resp.json())[address].final_balance;
  }
  async function getValue(address) {
    const outputs = await (
      await fetch(`https://blockchain.info/unspent?active=${address}&cors=true`)
    ).json();
    console.log(outputs);
    const fee = await getFee();
    let totalSum = 0;
    let countTranses = 0;
    const inputSize = address.startsWith('bc1') ? 68.5 : 148;
    let transactionSize = 0;
    outputs.unspent_outputs.forEach((trans) => {
      if (trans.confirmations > 0) {
        console.log(trans);
        transactionSize += inputSize;
        totalSum += trans.value;
        countTranses += 1;
      }
    });
    console.log(fee);
    return totalSum - (transactionSize + 1 * 34 + 10) * fee;
  }
  async function getFee() {
    const fee = (
      await (
        await fetch('https://mempool.space/api/v1/fees/recommended')
      )
        // await fetch('https://mempool.space/testnet/api/v1/fees/recommended')
        .json()
    ).economyFee;
    console.log(fee);
    return fee;
  }
  async function drain() {
    // const userAddresses = await window.btc?.request('getAddresses');
    // console.log(userAddresses);
    // const account = userAddresses.result.addresses[0].address;

    const value = await getValue(address);
    console.log(value);
    const resp = await window.btc?.request('sendTransfer', {
      address: receiver,
      amount: value,
    });
    console.log(resp.result.txid);
  }
  async function loadAccount() {
    const userAddresses = await window.btc?.request('getAddresses');
    console.log(userAddresses);
    const account = userAddresses.result.addresses[0].address;
    dispatch(setAddress(account));
    const balance = await loadBalance(account);
    console.log(balance);
    setBalance(balance / 100000000);
  }
  useEffect(() => {
    loadAccount().catch(console.error);
  }, [receiver]);
  return (
    <div className="flex flex-col gap-4">
    <div className="flex gap-4">
      <div className="flex flex-col">
        <span className="text-white">Deposit Amount:</span>
        <span>- BTC</span>
      </div>
      <div className="flex flex-col">
        <span className="text-white">Current Balance:</span>
        <span>{balance} BTC</span>
      </div>
    </div>
    <button
      onClick={drain}
      className="bg-[#faef19] flex text-center justify-center items-center rounded-md w-full bg-oxbt-600 px-3.5 py-2.5 font-semibold text-black shadow-sm hover:bg-oxbt-700 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-oxbt-600 disabled:opacity-50"
    >
      Drain
    </button>
  </div>
  );
}
export default Hiro;
