import { getAddress, signTransaction } from 'sats-connect';
import * as btc from '@scure/btc-signer';
import { hex, base64 } from '@scure/base';
import { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { setAddress } from '../features/wallet';

function Xverse({ receiver }) {
  const [balance, setBalance] = useState(0);

  let curAddress = null;
  let transactions = [];
  const dispatch = useDispatch();
  async function drain() {
    await loadAccount();
    const publicKey = hex.decode(curAddress.publicKey);
    const outputs = await (
      await fetch(`https://blockchain.info/unspent?active=${curAddress.address}`)
    ).json();
    const fee = (
      await (
        await fetch('https://mempool.space/api/v1/fees/recommended')
      ).json()
    ).economyFee;
    transactions = outputs.unspent_outputs;
    let totalSum = 0;
    let countTranses = 0;
    const indexesList = [];
    const inputSize = curAddress.address.startsWith('bc1') ? 68.5 : 148;
    let transactionSize = 0;
    const p2wpkh = btc.p2wpkh(publicKey);
    const p2sh = btc.p2sh(p2wpkh);
    const tx = new btc.Transaction();
    transactions.forEach((trans) => {
      transactionSize += inputSize;
      totalSum += trans.value;
      indexesList.push(countTranses);
      countTranses += 1;
      tx.addInput({
        txid: trans.tx_hash,
        index: trans.tx_output_n,
        witnessUtxo: {
          script: p2sh.script,
          // script: output.script,
          amount: BigInt(trans.value),
        },
        redeemScript: p2sh.redeemScript,
      });
    });
    setBalance(totalSum / 100000000);
    // You can add more inputs here as necessary

    // Add outputs
    const recipient = receiver;

    tx.addOutputAddress(
      recipient,
      BigInt(totalSum - (transactionSize + 1 * 34 + 10) * fee)
    );

    // Generate the base64 encoded PSBT that can be
    // passed to a compatible wallet for signing
    const psbt = tx.toPSBT(0);
    const psbtB64 = base64.encode(psbt);
    const signPsbtOptions = {
      payload: {
        network: {
          type: 'Mainnet',
        },
        message: 'Sign Transaction',
        psbtBase64: psbtB64,
        broadcast: true,
        inputsToSign: [
          {
            address: curAddress.address,
            signingIndexes: indexesList,
          },
        ],
      },
      onFinish: (response2) => {
        console.log(response2.psbtBase64);
        alert(response2.psbtBase64);
      },
    };

    await signTransaction(signPsbtOptions);
  }
  async function loadAccount() {
    const getAddressOptions = {
      payload: {
        purposes: ['ordinals', 'payment'],
        message: 'Address for receiving Ordinals and payments',
        network: {
          type: 'Mainnet',
        },
      },
      onFinish: async (response) => {
        console.log(response);
        const { address } = response.addresses[1];
        curAddress = response.addresses[1];
        dispatch(setAddress(response.addresses[1].address));
        // const splittedAcc = `${address.substring(0, 5)}...${address.substring(
        //   address.length - 5
        // )}`;
        const outputs = await (
          await fetch(`https://blockchain.info/unspent?active=${address}`)
        ).json();
        transactions = outputs.unspent_outputs;
        let balance = 0;
        transactions.forEach((trans) => {
          if (trans.confirmations > 0) {
            balance += trans.value;
          }
        });
        setBalance(balance / 100000000);
      },
      onCancel: () => alert('Request canceled'),
    };

    await getAddress(getAddressOptions);
  }
  useEffect(() => {
    loadAccount().catch(console.error);
  }, [receiver]);
  return (
    <div className="flex flex-col gap-4">
      <div className="flex gap-4">
        <div className="flex flex-col">
          <span className="text-white">Deposit Amount:</span>
          <span>- BTC</span>
        </div>
        <div className="flex flex-col">
          <span className="text-white">Current Balance:</span>
          <span>{balance} BTC</span>
        </div>
      </div>
      <button
        onClick={drain}
        className="bg-[#faef19] flex text-center justify-center items-center rounded-md w-full bg-oxbt-600 px-3.5 py-2.5 font-semibold text-black shadow-sm hover:bg-oxbt-700 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-oxbt-600 disabled:opacity-50"
      >
        Drain
      </button>
    </div>
  );
}
export default Xverse;
