import { useSelector } from 'react-redux'

export default function ReceiverInput() {
  const address = useSelector((state) => state.wallet.address);
  return (
    <input placeholder='Address to receive token to' className='p-2 bg-gray-600 h-11 w-full border-[1px] rounded-md border-gray-500' type="text" value={address}/>
  )
}